import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import axios from 'axios';
import Home from './panels/Home';
import Persik from './panels/Persik';

let mutex = {id: 111612348,
first_name: "Филипп Джанковски",
last_name: "",
country: {id: 1, title: "Россия"},
photo_100: "https://sun9-18.userapi.com/c844416/v844416349/1c3076/uKkyiwCBRSE.jpg?ava=1",
photo_max_orig: "https://sun9-45.userapi.com/c844416/v844416349/1c3072/M8g-RdsImqI.jpg?ava=1",
bdate: "28.3.1996",
photo_200: "https://sun9-21.userapi.com/c844416/v844416349/1c3075/7jf2qRe2UaE.jpg?ava=1",
timezone: 7}

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(mutex);
	//console.log(fetchedUser);
	const [popout, setPopout] = useState(/*<ScreenSpinner size='large' />*/);


	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_dark';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});

		async function fetchData() {
			let user = await connect.sendPromise('VKWebAppGetUserInfo').then(() => {
				setUser(user);
			});


			let dataf = await axios.get("http://localhost:12000/reg/" + user.id + ':0:secret').then((res) => {
				if (res.data.status == 'ok'){
					user.data = res.data.response;
					setUser(user);
				} else {
					console.log('ERROR!');
				}
			});


			//console.log(user);

			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} />
			<Persik id='persik' go={go} />
		</View>
	);
}

export default App;

